import React, { ChangeEvent, KeyboardEvent, useEffect, useRef, useState } from 'react'
import './style.css'
import { useLocation, useNavigate, useParams } from 'react-router-dom'
import { MAIN_PATH, SEARCH_PATH, USER_PATH, AUTH_PATH, BOARD_DETAIL_PATH, BOARD_WRITE_PATH, BOARD_UPDATE_PATH, BOARD_PATH } from 'constant';
import { useCookies } from 'react-cookie';
import { useBoardStore, useLoginUserStore } from 'stores';
import { fileUploadRequest, patchBoardRequest, postBoardRequest } from 'apis';
import { PatchBoardRequestDto, PostBoardRequestDto } from 'apis/request/board';
import PostBoardResponseDto from '../../apis/response/board/post-board.response.dto';
import { ResponseDto } from 'apis/response';
import { PatchBoardResponseDto } from 'apis/response/board';


// component :  
export default function Header() {

    // state :   
    const {loginUser, setLoginUser, resetloginUser} = useLoginUserStore();
    // state : path 
    const { pathname } = useLocation(); 
    // state : cookie 
    const [cookies, setCookies] = useCookies();
    // state :  
    const [isLogin, setLogin] = useState<boolean>(false);
    
    // state :    
    const [isAuthPage, setAuthPage] = useState<boolean>(false);
    // state :    
    const [isMainPage, setMainPage] = useState<boolean>(false);
    // state :    
    const [isSearchPage, setSearchPage] = useState<boolean>(false);
    // state :     
    const [isBoardDetailPage, setBoardDetailPage] = useState<boolean>(false);
    // state :     
    const [isBoardWritePage, setBoardWritePage] = useState<boolean>(false);
    // state :     
    const [isBoardUpdatePage, setBoardUpdatePage] = useState<boolean>(false);
    // state :    
    const [isUserPage, setUserPage] = useState<boolean>(false);

    
    // function :  
    const navigate = useNavigate();

    // event Handler :     
    const onLogoClickHandler = () => {
        navigate(MAIN_PATH());
    }

    // component :    
    const SearchButton = () => {

        //state :     
        const searchButtonRef = useRef<HTMLDivElement | null>(null)
        // state :   
        const [status, setStatus] = useState<boolean>(false);
        // state :  
        const [word, setWord] = useState<string>('');
        // state :  path variable  // App.tsx SEARCH_PATH(':searchWord')   .
        const {searchWord} = useParams();

        
        // event handler :     
        const onwordChangeHandler = (event: ChangeEvent<HTMLInputElement>) =>{
            const value = event.target.value;
            setWord(value);
        };
        
        // event handler :     
        const onwordKeyDownHandler = (event:KeyboardEvent<HTMLInputElement>) => {
            //    enter  
            if(event.key !== 'Enter') return;
            //   searchButtonRef  div      
            if(!searchButtonRef.current) return;
            // searchButtonRef .
            searchButtonRef.current.click();
        };

        // event handler :      
        const onSearchButtonClickHandler = () => {
            if(!status) {
                setStatus(!status);
                return;
            }
            navigate(SEARCH_PATH(word));
        };
        
        // effect :  path variable    
        useEffect(() => {
            //      
            if(searchWord){
                setWord(searchWord);
                //true        .
                setStatus(true);
            }
            
        },[searchWord]);

        
        if(!status)
        // render    false 
        return (
            <div className='icon-button' onClick={onSearchButtonClickHandler}>
                <div className='icon search-light-icon'></div>
            </div>
        );
        // render    true 
        return (
            <div className='header-search-input-box'>
                <input className='header-search-input' type="text" placeholder=' .' value={word} onChange={onwordChangeHandler} onKeyDown={onwordKeyDownHandler}/>
                <div ref={searchButtonRef} className='icon-button' onClick={onSearchButtonClickHandler}>
                    <div className='icon search-light-icon'></div> 
                </div>
            </div>
        );
    };

    // component :   
    const MyPageButton = () =>{

        // state : userEmail path variable 
        const { userEmail } = useParams();


        // event handler :      
        const onMyPageButtonClickHandler = () =>{
            if(!loginUser) return;
            const {email} = loginUser;
            navigate(USER_PATH(email));
        };
        // event handler :      
        const onSignOutButtonClickHandler = () =>{
            resetloginUser();
            setCookies('accessToken', '', {path : MAIN_PATH(), expires:new Date()})
            navigate(MAIN_PATH());
        };
        // event handler :      
        const onSignInButtonClickHandler = () =>{
            navigate(AUTH_PATH());
        };

        // render :  
        if(isLogin && userEmail === loginUser?.email) 
        return <div className='white-button' onClick={onSignOutButtonClickHandler}>{''}</div>
        if(isLogin)
        // render :   
        return <div className='white-button' onClick={onMyPageButtonClickHandler}>{''}</div>
        // render :   
        return <div className='black-button' onClick={onSignInButtonClickHandler}>{''}</div>
    };
    
    // component :   
    const UploadButton = () => {

        // state :   path variale 
        const { boardNumber } = useParams();
        // state :   
        const {title, content, boardImageFileList, resetBoard} = useBoardStore();

        // function : post board response  
        const postBoardResponse = (responseBody : PostBoardResponseDto | ResponseDto | null) => {
            if(!responseBody) return ;
            const { code } = responseBody;
            if(code === 'AF' || code === 'NU') navigate(AUTH_PATH());
            if(code === 'VF') alert('  .');
            if(code !== 'SU') return;

            resetBoard();
            if(!loginUser) return;
            const { email } = loginUser;
            navigate(USER_PATH(email));
        }

        // function : patch board response  
        const patchBoardResponse = (responseBody: PatchBoardResponseDto | ResponseDto | null) => {
            if(!responseBody) return ;
            const { code } = responseBody;
            if(code === 'DBE') alert(' .')
            if(code === 'AF' || code === 'NU' || code === 'NB' || code === 'NP') navigate(AUTH_PATH());
            if(code === 'VF') alert('  .');
            if(code !== 'SU') return;

            if(!boardNumber) return;
            navigate(BOARD_PATH() + '/' + BOARD_DETAIL_PATH(boardNumber));
        }

        // event handler :     
        const onUploadButtonClickHandler = async () =>{
            const accessToken = cookies.accessToken;
            if(!accessToken) return;


            const boardImageList: string[] = [];
            
            //    URL  .
            for(const file of boardImageFileList) {
                const data = new FormData();
                data.append('file',file);
                
                const url = await fileUploadRequest(data);
                if(url) boardImageList.push(url);
            }

            const isWriterPage = pathname === BOARD_PATH() + '/' + BOARD_WRITE_PATH();
            if(isWriterPage){
                const requestBody: PostBoardRequestDto = {
                    title, content, boardImageList
                }
                postBoardRequest(requestBody, accessToken).then(postBoardResponse);
            } else {
                if(!boardNumber) return;
                const requestBody : PatchBoardRequestDto = {
                    title, content, boardImageList
                }
                patchBoardRequest(boardNumber, requestBody, accessToken).then(patchBoardResponse);
            }
        }

        // render :   
        if(title && content)
        return <div className='black-button' onClick={onUploadButtonClickHandler}>{''}</div>
        // render :    
        return <div className='disable-button'>{''}</div>
        
    }

    // effect : path  
    useEffect(() => {
        const isAuthPage = pathname.startsWith(AUTH_PATH());
        setAuthPage(isAuthPage);
        const isMainPage = pathname === MAIN_PATH();
        setMainPage(isMainPage);
        const isSearchPage = pathname.startsWith(SEARCH_PATH(''));
        setSearchPage(isSearchPage);
        const isBoardDetailPage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_DETAIL_PATH(''));
        setBoardDetailPage(isBoardDetailPage);
        const isBoardWritePage = pathname.startsWith(BOARD_PATH() + '/' +BOARD_WRITE_PATH());
        setBoardWritePage(isBoardWritePage);
        const isBoardUpdatePage = pathname.startsWith(BOARD_PATH() + '/' +BOARD_UPDATE_PATH(''));
        setBoardUpdatePage(isBoardUpdatePage);
        const isUserPage = pathname.startsWith(USER_PATH(''));
        setUserPage(isUserPage);
    },[pathname])

    // effect : login user  
    useEffect(() => {
        setLogin(loginUser !== null);
    }, [loginUser])

    //render
    return (
        <div id='header'>
            <div className='header-container'>
                <div className='header-left-box' onClick={onLogoClickHandler}>
                    <div className='icon-box'>
                        <div className='icon logo-dark-icon'></div>
                    </div>
                    <div className='header-logo'>{'JangGeol\'s Board'}</div>
                </div>
                <div className='header-right-box'>
                    {(isAuthPage || isMainPage || isSearchPage || isBoardDetailPage) && <SearchButton/>}
                    {(isMainPage || isSearchPage || isBoardDetailPage || isUserPage) && <MyPageButton/>}
                    {(isBoardWritePage || isBoardUpdatePage) && <UploadButton/>}
                </div>
            </div>
            
        </div>
    )
}
